<template>
  <div class="lg:w-full">
    <img
      :src="selectedImage.imgSrc"
      class="object-cover h-72 mb-10 lg:mb-0 lg:w-4/5 lg:h-full picture"
    />
    <div class="flex flex-row justify-center space-x-6 text-center mb-20">
      <button class="lg:hidden" @click="prevImg">
        <font-awesome-icon class="text-brown-700 text-xl" icon="arrow-left" />
      </button>

      <button class="lg:hidden" @click="nextImg">
        <font-awesome-icon class="text-brown-700 text-xl" icon="arrow-right" />
      </button>
    </div>
  </div>
</template>
<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import { faArrowLeft, faArrowRight } from "@fortawesome/free-solid-svg-icons";
library.add(faArrowLeft, faArrowRight);

export default {
  name: "ImageCarousel",
  props: {
    images: {
      require: true,
      type: Array,
    },
  },
  data() {
    return {
      imageIndex: 0,
    };
  },
  computed: {
    selectedImage() {
      return this.images[this.imageIndex];
    },
  },
  methods: {
    prevImg() {
      if (this.imageIndex <= 0) {
        this.imageIndex = this.images.length - 1;
      } else {
        this.imageIndex--;
      }
    },
    nextImg() {
      this.imageIndex = (this.imageIndex + 1) % this.images.length;
    },
    autoNextImg() {
      if (window.innerWidth > 1024) this.nextImg();
    },
  },
  mounted() {
    setInterval(this.autoNextImg, 4000);
  },
  unmounted() {
    clearInterval(this.autoNextImg);
  },
};
</script>

<style scoped>
@media (min-width: 1024px) {
  .picture {
    aspect-ratio: 1 / 1;
    border-top-left-radius: 999px;
    border-top-right-radius: 999px;
  }
  @media (min-width: 1024px) {
    .picture {
      aspect-ratio: unset;
    }
  }
}
</style>
