<template>
  <div class="lg:text-base">
    <div class="relative">
      <img
        src="@/assets/images/sparkles.svg"
        alt="sparkle"
        class="hidden lg:inline lg:absolute"
        :class="starsPositionClass"
      />
      <img
        style="aspect-ratio: 1/1"
        class="object-cover"
        :src="summaryCard.imgSrc"
        :alt="summaryCard.imgAlt"
      />
    </div>

    <h2 class="py-7 ml-7 tracking-widest lg:ml-0">
      {{ summaryCard.title }}
    </h2>
    <Button class="ml-7 lg:ml-0 lg:mt-auto lg:self-start">Zobacz więcej</Button>
  </div>
</template>

<script>
import Button from "./Button.vue";

export default {
  name: "BaptismSummaryCard",
  components: {
    Button,
  },
  props: {
    summaryCard: {
      require: true,
      type: Object,
    },
  },
  data: () => ({
    starsPosition: Math.floor(Math.random() * 4),
  }),
  computed: {
    starsPositionClass() {
      switch (this.starsPosition) {
        case 0:
          return "-left-8 -top-6";
        case 1:
          return "-right-8 -top-5";
        case 2:
          return "-right-8 -bottom-1";
        case 3:
          return "-left-9 -bottom-2";
      }
      return "";
    },
  },
};
</script>
